.tab-title {
	padding: 20px 50px 20px 20px;
	font-weight: 600;
	font-size: 18px;
	line-height: 24px;
	cursor: pointer;
	position: relative;
}

.tab-title::before {
	content: "";
	display: inline-block;
	width: 0;
	height: 0;
	position: absolute;
	border-left: 10px solid transparent;
	border-right: 10px solid transparent;
	border-top: 10px solid #fff;
	vertical-align: middle;
	right: 20px;
	top: 50%;
	margin-top: -7px;
	transition: transform 0.5s;
	transform: rotate(0deg);
	z-index: 1;
}

.tab-title::after {
	content: "";
	display: inline-block;
	width: 0;
	height: 0;
	position: absolute;
	border-left: 10px solid transparent;
	border-right: 10px solid transparent;
	border-top: 10px solid #000;
	vertical-align: middle;
	right: 20px;
	top: 50%;
	margin-top: -5px;
	transition: transform 0.5s;
	transform: rotate(0deg);
}

.tab-panel {
	padding: 0 20px 30px 20px;
	display: none;
}

.wp-block-wpgb-captain-block-tab.tab-panel-wrapper {
	margin-bottom: 20px;
	box-shadow: 0px 5px 25px 0px rgba(0, 0, 0, 0.06);
	border-radius: 10px;
	background:#fff
}

.tab-panel p:last-child {
	margin: 0;
}

.tab-title.active::before,
.tab-title.active::after {
	transform: rotate(180deg);
}
